import { defineComponent, ref } from 'vue';
export default defineComponent({
    // import {ref} from 'vue';
    // export default {
    setup: function () {
        var name = ref('pixi-vue');
        var left = ref(0);
        var pos = ref([20, 120, 200, 100]);
        var count = ref(1);
        var setName = function (n) {
            name.value = n;
            pos.value[1] += 20;
            count.value += 1;
            left.value += 2;
        };
        return {
            name: name,
            setName: setName,
            pos: pos,
            count: count,
            left: left,
        };
    }
});
